.footer {
  position: relative;
  background: #F2F5F8;
  flex: 0 0 auto;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 117.6rem;
    width: 100%;
    margin: 0 auto;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__logo {
    flex: 0 0 16.9rem;
    width: 16.9em;
    height: 3.4rem;
    background: url('../images/logo.svg') center/100% 100% no-repeat;
    margin-right: 6rem;
  }
  &__logo-link {
    display: block;
    height: 100%;
  }
  &__nav {
    flex: 0 0 auto;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__copy {
    font-size: 1.2rem;
    opacity: .5;
    margin-right: 5rem;
  }
}