.section {
  position: relative;
  padding-top: 8rem;
  margin-bottom: 12rem;
  min-height: 48rem;
  &--small {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 1.2rem;
  }
  &__img {
    position: absolute;
    max-width: 100%;
    height: auto;
    top: 0;
    left: 0;
    right: 0;
    background: #001837;
    overflow: hidden;
    box-shadow: 0 2rem 3rem rgba(1, 25, 54, 0.20389);
    border-radius: 20rem 20rem 0 0;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: #001837;
      opacity: .7;
    }
    img {
      width: 100%;
    }
  }
  &__title {
    text-align: center;
    position: relative;
    color: #fff;
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 3.4rem;
    text-transform: uppercase;
    margin-bottom: 2.3rem;
  }
  &__descriptions {
    position: relative;
    text-align: center;
    color: #fff;
    max-width: 86rem;
    margin: 0 auto 3rem;
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
  &__btn {
    position: relative;
    text-align: center;
    margin-bottom: 6.8rem;
  }
  &__list {
    position: relative;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  &__item {
    position: relative;
    flex: 0 0 auto;
    margin-left: 2rem;
    margin-right: 2rem;
    text-decoration: none;
    margin-bottom: 4rem;
    @include desktop-hover() {
      .section__item-title {
        color: $red;
        &::before {
          opacity: 1;
        }
      }
      .section__item-img {
         box-shadow: 0 2rem 3rem rgba(1, 25, 54, 0.0966182);
       }
    }
  }
  &__item-img {
    width: 26.5rem;
    height: 26.5rem;
    max-width: 26.5rem;
    max-height: 26.5rem;
    overflow: hidden;
    background: #fff;
    box-shadow: 0 .4rem 1.6rem rgba(0, 0, 0, 0.102546);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    margin-bottom: 3.5rem;
    border-radius: 50%;
    transition: .3s ease-in-out;
  }
  &__item-title {
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 2.4rem;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #011936;
    padding-bottom: 2.4rem;
    position: relative;
    text-align: center;
    transition: .3s ease-in-out;
    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      background: #BC2C00;
      width: 3rem;
      height: 2px;
      left: 50%;
      margin-left: -1.5rem;
      opacity: 0;
      transition: .3s ease-in-out;
    }
  }
  &__list2 {
    position: relative;
    display: flex;
    overflow: hidden;
    margin-bottom: 4rem;
    box-shadow: 0px .5rem 2rem rgba(0, 0, 0, 0.08);
    border-radius: .8rem;
    background: #fff;
  }
  &__list2-item {
    width: 100%;
    border-right: 1px solid #EAEAEA;
    padding: 3rem 4rem 6.4rem;
    &:last-child {
      border-right: 0;
    }
  }
  &__list2-img {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 2rem;
  }
  &__list2-itemDescriptions {
    position: relative;
    margin-right: -2rem;
    &.text-content {
      color: #000;
    }
  }
  &__list3 {
    position: relative;
    margin-top: 8rem;
  }
  &__list3-item {
    background: #fff;
    box-shadow: 0px .5rem 2rem rgba(0, 0, 0, 0.08);
    border-radius: .8rem;
    margin-bottom: 4rem;
    padding: 4rem 17rem;
    text-align: center;
    position: relative;
    &::before {
      content: '';
      position: absolute; 
      top: 100%;
      left: 0;
      right: 0;
      height: 3px;
      background: $red;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}