.page-info {
  position: relative;
  display: flex;
  max-width: 119rem;
  width: 100%;
  margin: 0 auto;
  padding: 8rem 0;
  &__title {
    flex: 0 0 47.6rem;
    max-width: 47.6rem;
    width: 100%;
    padding-right: 12.5rem;
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 3.4rem;
    line-height: 3.4rem;
    text-transform: uppercase;
  }
}