.first-screen {
  position: relative;
  padding-top: 16rem;
  margin-bottom: 16rem;
  &__container {
    display: flex;
  }
  &__info {
    margin-right: 6rem;
    flex: 0 0 72rem;
    max-width: 72rem;
    width: 100%;
    padding-top: 10rem;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      background: url('../images/decor.svg') center/100% 100% no-repeat;
      width: 40rem;
      height: 45.2rem;
      top: 3rem;
      right: 3rem;
    }
  }
  &__title {
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 6.4rem;
    text-transform: uppercase;
    color: #011936;
    margin-bottom: 6rem;
  }
  &__descriptions {
    padding-left: 3rem;
    border-left: 1px solid $red;
    max-width: 56.8rem;
  }
  &__img {
    flex: 0 0 90rem;
    box-shadow: 0 2rem 3rem rgba(1, 25, 54, 0.20389);
    border-radius: 20rem 0 0 0;
    background: #000;
    overflow: hidden;
  }
}