.descriptions2 {
  position: relative;
  &__item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 12rem;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      background: url('../images/decor.svg') center/100% 100% no-repeat;
      width: 40rem;
      height: 45.2rem;
      bottom: 0;
      right: -20rem;
    }
    &:nth-child(even) {
      flex-direction: row-reverse;
      &::before {
        display: none;
      }
      .descriptions2__item-img {
        margin-left: 0;
        margin-right: 11rem;
      }
    }
  }
  &__text {
    flex: 1 1 auto;
    position: relative;
  }
  &__title {
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 3.4rem;
    text-transform: uppercase;
    margin-bottom: 3rem;
  }
  &__item-img {
    position: relative;
    flex: 0 0 48rem;
    max-width: 48rem;
    height: 48rem;
    background: #fff;
    box-shadow: 0px 2rem 3rem rgba(0, 24, 55, 0.20012);
    margin-left: 11rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
  }
}