.red-section {
  position: relative;
  display: flex;
  justify-content: flex-end;
  &::before {
    content: '';
    position: absolute;
    background: url('../images/decor.svg') center/100% 100% no-repeat;
    width: 40rem;
    height: 45.2rem;
    top: -10rem;
    right: 135rem;
    @include response(1439px) {
      right: 125rem;
    }
  }
  &--first {
    margin-bottom: 8rem;
    &::before {
      top: -22rem;
    }
  }
  &__inner {
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
    border-radius: 20rem 0 0 0;
    flex: 0 0 155rem;
    max-width: 155rem;
    box-shadow: 0px 2rem 3rem rgba(188, 44, 0, 0.196897);
    min-height: 52rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include response(1439px) {
      flex: 0 0 145rem;
      max-width: 145rem;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: #BC2C00;
      opacity: .9;
    }
  }
  &__list {
    padding: 5.8rem 12.5rem 4.8rem;
    position: relative;
  }
  &__item {
    margin-bottom: 5rem;
    display: flex;
    align-items: flex-start;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__count {
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 3.4rem;
    text-transform: uppercase;
    margin-right: 5.8rem;
    position: relative;
    color: #fff;
    padding-top: .7rem;
    &::before {
      content: '';
      position: absolute;
      left: -2.4rem;
      top: 50%;
      margin-top: -3px;
      width: 0;
      height: 0;
      border-top: 4px solid transparent;
      border-left: 6px solid #fff;
      border-bottom: 4px solid transparent;
    }
  }
  &__item-text.text-content {
    color: #fff;
    max-width: 96rem;
    font-size: 1.8rem;
  }
  &__content {
    position: relative;
    padding: 11rem 15rem 9rem;
  }
  &__title {
    color: #fff;
    margin-bottom: 3.3rem;
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 6.4rem;
    text-transform: uppercase;
  }
  &__text {
    color: #fff;
    font-size: 1.8rem;
    line-height: 3.2rem;
    max-width: 96rem;
    p {
      margin: 0 0 3rem;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}