.text {
  position: relative;
  padding-top: 8rem;
  padding-bottom: 8rem;
  &--mb120 {
    margin-bottom: 12rem;
  }
  &__box {
    display: flex;
  }
  &__content {
    flex: 1 1 auto;
    max-width: 86rem;
  }
  &__decor {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 5rem 5rem 4rem 8rem;
    flex: 0 0 48rem;
    max-width: 48rem;
    margin-left: 11rem;
  }
  &__decor-row {
    position: relative;
    color: #fff;
    font-size: 1.8rem;
    margin-bottom: 3rem;
    line-height: 3.2rem;
    &::before {
      content: '';
      position: absolute;
      left: -3rem;
      top: 1.2rem;
      width: 0;
      height: 0;
      border-top: 4px solid transparent;
      border-left: 6px solid #BC2C00;
      border-bottom: 4px solid transparent;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__list {
    margin-top: 4rem;
    display: flex;
    margin-left: -5rem;
  }
  &__list-item {
    flex: 0 0 calc(33.333% - 5rem);
    max-width: calc(33.333% - 5rem);
    margin-left: 5rem;
  }
}