.page-descriptions {
  position: relative;
  padding: 8rem 0;
  &__box {
    display: flex;
    align-items: center;
  }
  &__img {
    flex: 0 0 34rem;
    max-width: 34rem;
    height: 34rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 6rem;
    padding: 3rem;
  }
  &__text {
    flex: 0 0 70rem;
    max-width: 70rem;
  }
}