.info4 {
  position: relative;
  padding-top: 8rem;
  padding-bottom: 14rem;
  &__description {
    max-width: 86rem;
    width: 100%;
    margin: 0 auto 12rem;
    text-align: center;
    &--pt40 {
      padding-top: 4rem;
    }
  }
  &__text {
    margin-bottom: 4rem;
    max-width: 86rem;
  }
  &__list {
    position: relative;
  }
  &__item {
    position: relative;
    margin-bottom: 4rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__item-text {
    padding-left: 2.7rem;
    max-width: 86rem;
  }
}