.type-products {
  position: relative;
  &__list {
    margin-left: -4rem;
    display: flex;
    flex-wrap: wrap;
    position: relative;
  }
  &__item {
    text-decoration: none;
    overflow: hidden;
    box-shadow: 0px .5rem 2rem rgba(0, 0, 0, 0.08);
    border-radius: .8rem;
    background: #fff;
    flex: 0 0 calc(50% - 4rem);
    margin-left: 4rem;
    margin-bottom: 4rem;
    padding: 0 5.2rem 2.2rem;
    height: 44rem;
    @include desktop-hover() {
      box-shadow: 0px .8rem 3rem rgba(0, 0, 0, 0.1);
    }
  }
  &__item-img {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 3rem 1.5rem 2rem;
  }
  &__item-title {
    margin-bottom: 1.8rem;
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 3.4rem;
    text-align: center;
    text-transform: uppercase;
    color: $red;
  }
  &__item-descriptions {
    text-align: center;
    max-height: 9.6rem;
    overflow: hidden;
  }
  &__content {
    text-align: center;
    max-width: 86rem;
    width: 100%;
    margin: 0 auto;
  }
}