.btn {
  min-width: 18.4rem;
  height: 4.8rem;
  background: $red;
  color: #fff;
  outline: none;
  border: 0;
  cursor: pointer;
  padding: 1rem 2rem;
  color: #fff;
  text-decoration: none;
  font-weight: 500;
  font-size: 1.4rem;
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
  position: relative;
  svg {
    position: relative;
    fill: #fff;
  }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: -2rem;
    background: #001837;
    width: 7.5rem;
    transform: skew(15deg);
    transition: .3s ease-in-out;
  }
  @include desktop-hover() {
    &::before {
      transform: skew(15deg) translateX(-.5rem);
    }
  }
  &--white {
    &::before {
      background: #fff;
    }
    svg {
      fill: $red;
    }
  }
}

.btn-2 {
  position: relative;
  height: 4.8rem;
  display: inline-flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  outline: none;
  color: #fff;
  padding: 1rem 3.6rem;
  border: 1px solid #fff;
  background: transparent;
  font-weight: 500;
  font-size: 14px;
  text-decoration: none;
  @include desktop-hover() {
    background: #fff;
    color: #000;
  }
}