.info2 {
  position: relative;
  background: #7DA0B8;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: url('../images/decor2.png') left top/6.7rem 8.5rem repeat;
  }
  &__container {
    padding: 10rem 0;
    min-height: 45.2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  &__title {
    position: relative;
    color: #fff;
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 6.4rem;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 4.5rem;
  }
  &__descriptions {
    color: #fff;
    font-size: 1.8rem;
    line-height: 3.2rem;
    text-align: center;
    max-width: 86rem;
    width: 100%;
    p {
      margin: 0 0 3rem;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}