.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  transition: .3s ease-in-out;
  &--bg {
    background: #fff;
    box-shadow: 0 0.4rem 1.6rem rgba(0, 0, 0, 0.102546);
  }
  &__box {
    max-width: 144rem;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__logo {
    flex: 0 0 16.9rem;
    width: 16.9em;
    height: 3.4rem;
    margin-right: 4rem;
    background: url('../images/logo.svg') center/100% 100% no-repeat;
    .header--white & {
      background: url('../images/logo2.svg') center/100% 100% no-repeat;
    }
    .header--bg & {
      background: url('../images/logo.svg') center/100% 100% no-repeat;
    }
  }
  &__logo-link {
    display: block;
    height: 100%;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__tel-list {
    flex: 0 0 auto;
    margin-right: 3rem;
  }
  &__tel {
    font-weight: 500;
    font-size: 1.4rem;
    line-height: 1.6rem;
    display: block;
    color: #000;
    text-decoration: none;
    .header--white & {
      color: #fff;
    }
    .header--bg & { 
      color: #000;
    }
    @include desktop-hover() {
      color: $red;
    }
  }
  &__search {
    padding: 1rem;
    cursor: pointer;
    margin-right: 3.5rem;
    svg {
      fill: #000;
      .header--white & {
        fill: #fff;
      }
      .header--bg & {
        fill: #000;
      }
    }
    @include desktop-hover() {
      svg {
        fill: $red;
      }
    }
  }
}