.title {
  text-align: center;
  font-family: 'Bebas';
  font-weight: 700;
  font-size: 3.4rem;
  text-transform: uppercase;
  color: #000;
  margin-bottom: 5rem;
  position: relative;
  &--decor {
    &::before {
      content: '';
      position: absolute;
      bottom: -2.3rem;
      background: $red;
      width: 3rem;
      height: 2px;
      left: 50%;
      margin-left: -1.5rem;
    }
  }
  &--mb20 {
    margin-bottom: 2rem;
  }
  &--left {
    text-align: left;
  }
}

.title-page-descriptions {
  text-align: center;
  max-width: 860px;
  margin: 0 auto 4rem;
}