.for-whom {
  position: relative;
  margin-bottom: 9rem;
  &__title {
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 3.4rem;
    text-transform: uppercase;
    margin-bottom: 9.3rem;
    text-align: center;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: 14.5rem;
  }
  &__item {
    flex: 0 0 33.33%;
    max-width: 33.333%;
    margin-bottom: 9rem;
    padding-right: 1rem;
    display: flex;
    align-items: center;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      left: -2.3rem;
      top: -2.3rem;
      width: 7.4rem;
      height: 7.4rem;
      background: #7DA0B8;
      opacity: 0.1;
      border-radius: 50%;
    }
  }
  &__icon {
    flex: 0 0 9.2rem;
    max-width: 9.2rem;
    width: 100%;
  }
  &__text {
    font-size: 1.8rem;
    flex: 1 1 auto;
  }
}