.form {
  position: relative;
  max-width: 38rem;
  width: 100%;
  margin: 0 auto;
  &__list {
    margin-bottom: 4rem;
  }
  &__row {
    display: block;
    position: relative;
    margin-bottom: 2.2rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__btn {
    text-align: center;
  }
}

.input-label {
  font-weight: 500;
  font-size: 1.4rem;
  opacity: 0.5;
  transition: .3s ease-in-out;
  position: absolute;
  left: 0;
  right: 0;
  top: 1.8rem;
  .js-parent-input.active & {
    transform: translateY(-2rem);
    font-size: 1rem;
    color: #575757;
  }
}
.input-style {
  width: 100%;
  background: transparent;
  outline: none;
  height: 5rem;
  border: 0;
  border-bottom: 1px solid #979797;
  transition: .3s ease-in-out;
  font-weight: 500;
  font-size: 1.4rem;
  .js-parent-input.active & {
    border-color: $red;
  }
  &:focus {
    border-color: $red;
  }
}
.textarea-style {
  width: 100%;
  background: transparent;
  outline: none;
  height: 5rem;
  border: 0;
  border-bottom: 1px solid #979797;
  transition: .3s ease-in-out;
  font-weight: 500;
  font-size: 1.4rem;
  resize: none;
  padding: 1.6rem 0;
  .js-parent-input.active & {
    border-color: $red;
  }
  &:focus {
    border-color: $red;
  }
}