.social {
  display: flex;
  align-items: center;
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  &__item {
    position: relative;
  }
  &__link {
    padding: 2rem;
    display: block;
    @include desktop-hover() {
      transform: translateY(-2px);
    }
  }
}