@mixin font($fontname, $filename, $fontweight, $fontstyle) {
  @font-face {
    font-family: $fontname;
    src: url(#{'../fonts/'+$filename+'.eot'});
    src:
      local('ðŸ˜¥'),
      url(#{'../fonts/'+$filename+'.woff2'}) format("woff2"),
      url(#{'../fonts/'+$filename+'.woff'}) format("woff"),
      url(#{'../fonts/'+$filename+'.ttf'}) format("truetype");
    font-weight: $fontweight;
    font-style: $fontstyle;
  }
}

@include font('Bebas', 'bebasneue_book-webfont', 300, normal);
@include font('Bebas', 'bebasneue_regular-webfont', 400, normal);
@include font('Bebas', 'bebasneue_bold-webfont', 700, normal);
