.advantages {
  position: relative;
  padding-top: 11rem;
  max-width: 112rem;
  width: 100%;
  margin: 0 auto;
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -6rem;
  }
  &__item {
    flex: 0 0 calc(50% - 6rem);
    max-width: calc(50% - 6rem);
    margin-left: 6rem;
    margin-bottom: 13rem;
    position: relative;
    &:nth-child(even) {
      .advantages__count {
        left: 90%;
      }
    }
  }
  &__count {
    position: absolute;
    top: -3rem;
    left: -13.2rem;
  }
  &__title {
    margin-bottom: 2.8rem;
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 3.4rem;
    text-transform: uppercase;
    position: relative;
    padding-left: 2.7rem;
    padding-right: 1rem;
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -3px;
      width: 0;
      height: 0;
      border-top: 4px solid transparent;
      border-left: 6px solid $red;
      border-bottom: 4px solid transparent;
    }
    &--2 {
      font-size: 2.4rem;
      margin-bottom: 2rem;
      &::before {
        margin-top: -5px;
      }
    }
  }
  &__text {
    position: relative;

  }
}