.top {
  border-radius: 0 0 20rem 20rem;
  height: 53rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding: 9.4rem 1.5rem 6.3rem;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #001837;
    opacity: .8;
  }
  &__title {
    position: relative;
    margin-bottom: 4rem;
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 6.4rem;
    color: #fff;
    text-transform: uppercase;
    text-align: center;
  }
  &__desciptions {
    position: relative;
    font-size: 1.8rem;
    line-height: 3.2rem;
    color: #fff;
    text-align: center;
    margin-bottom: 4rem;
    max-width: 98rem;
    p {
      margin: 0;
    }
  }
  &__btn {
    text-align: center;
  }
}