.list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -4.7rem;
  margin-bottom: 4rem;
  &__item {
    flex: 0 0 calc(50% - 4.7rem);
    max-width: calc(50% - 4.7rem);
    margin-top: 8rem;
    margin-left: 4.7rem;
    display: flex;
    align-items: flex-start;
    &:nth-child(1),
    &:nth-child(2) {
      margin-top: 0;
    }
  }
  &__item-img {
    flex: 0 0 14.2rem;
    max-width: 14.2rem;
    padding-right: 1rem;
  }
  &__item-info {
    flex: 1 1 autp;
  }
  &__item-title {
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 2.4rem;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #011936;
    margin-bottom: 2.5rem;
  }
}