body {
  height: 100%;
  font-family: 'Roboto', sans-serif;
  font-size: 1.6rem;
  line-height: 1;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
html {
  font-size: 62.5%;
  @include response-range(1600px,1850px) {
    font-size: 55%;
  }
  @include response-range(1200px, 1599px) {
    font-size: 7.5px;
  }
}
.wrapper {
  max-width: 192rem;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  flex: 1 1 auto;
}
.container {
  max-width: 144rem;
  width: 100%;
  margin: 0 auto;
  &--small {
    max-width: 118rem;
  }
}
.bg {
  background: #F2F5F8;
  padding-top: 8rem;
  padding-bottom: 8rem;
}
.text-red {
  color: $red;
}
a,svg {
  transition: .3s ease-in-out;
}
* {
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
