.home-info {
  position: relative;
  margin-bottom: 22rem;
  &::before {
    content: '';
    position: absolute;
    background: url('../images/decor.svg') center/100% 100% no-repeat;
    width: 40rem;
    height: 45.2rem;
    top: 50%;
    left: 0;
    top: -22rem;
  }
  &__container {
    display: flex;
    align-items: center;
  }
  &__content {
    padding-left: 12.5rem;
    padding-right: 5.6rem;
    flex: 0 0 77.6rem;
    max-width: 77.6rem;
    width: 100%;
  }
  &__content-title {
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 3.4rem;
    text-transform: uppercase;
    margin-bottom: 2.5rem;
  }
  &__triggers {
    flex: 0 0 90.4rem;
    max-width: 90.4rem;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 0 2rem 3rem rgba(188, 44, 0, 0.196897);
    overflow: hidden;
    border-radius: 20rem 0 0 0;
    position: relative;
    padding: 8.8rem 11rem 8rem;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: $red;
      opacity: .9;
    }
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 5rem;
    max-width: 41rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__img {
    flex: 0 0 9.4rem;
    max-width: 9.4rem;
    padding-right: 1rem;
  }
  &__text {
    color: #fff;
    font-family: Bebas Neue;
    font-weight: 700;
    font-size: 2.4rem;
    letter-spacing: .1rem;
    text-transform: uppercase;
  }
}