.breadcrumb {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0;
  position: relative;
  margin-bottom: 3rem;
  li {
    margin-right: 1.5rem;
    &:last-child {
      margin-right: 0;
    }
  }
  a {
    color: #fff;
    text-decoration: none;
    font-size: 1.4rem;
    line-height: 1.6rem;
    opacity: .8;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      right: -1.1rem;
      background: #fff;
      height: 1px;
      width: 7px;
      top: .8rem;
    }
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  span {
    opacity: .8;
    font-size: 14px;
    line-height: 16px;
    color: #B6B6B6;
  }
}