.category {
  position: relative;
  &__item {
    position: relative;
    padding: 8rem 1.5rem;
    &:nth-child(odd) {
      background: #F2F5F8;
    }
  }
  &__item-holder {
    max-width: 119rem;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: stretch;
  }
  &__item-img {
    background: #fff;
    box-shadow: 0px .5rem 2rem rgba(0, 0, 0, 0.08);
    border-radius: .8rem;
    flex: 0 0 26rem;
    max-width: 26rem;
    width: 100%;
    height: 26rem;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 6rem;
    @include desktop-hover() {
      box-shadow: 0px 1rem 3rem rgba(0, 0, 0, 0.1);
    }
  }
  &__item-info {
    flex: 1 1 auto;
  }
  &__item-title {
    display: block;
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 3.4rem;
    text-transform: uppercase;
    color: #BC2C00;
    margin-bottom: 2.2rem;
    text-decoration: none;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__item-text {
    margin-bottom: 2rem;
  }
  &__item-btn {
    text-decoration: none;
    color: $red;
    position: relative;
    padding-right: 5rem;
    font-weight: 500;
    font-size: 1.4rem;
    @include desktop-hover() {
      text-decoration: underline;
      &::before {
        transform: translateX(4px);
      }
    }
    &::before {
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      width: 3.6rem;
      height: 1px;
      background: $red;
      transition: .3s ease-in-out;
    }
  }
}