.text-content {
  line-height: 3.2rem;
  color: #575757;
  &--maw860 {
    max-width: 860px;
  }
  p {
    margin: 0 0 3rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    color: $red;
    text-decoration: none;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
}