.info5 {
  position: relative;
  padding-bottom: 12.5rem;
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10rem;
  }
  &__item {
    flex: 0 0 calc(50% - 10rem);
    max-width: calc(50% - 10rem);
    margin-top: 7rem;
    margin-left: 10rem;
    display: flex;
    align-items: flex-start;
    &:nth-child(1),
    &:nth-child(2) {
      margin-top: 0;
    }
  }
  &__count {
    padding-left: 2.4rem;
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 2.4rem;
    padding-top: 0.5rem;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #011936;
    position: relative;
    margin-right: 2rem;
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -3px;
      width: 0;
      height: 0;
      border-top: 4px solid transparent;
      border-left: 6px solid #BC2C00;
      border-bottom: 4px solid transparent;
    }
  }
}