.info3 {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10rem;
  &__item {
    padding-left: 5rem;
    flex: 0 0 calc(50% - 9rem);
    max-width: calc(50% - 9rem);
    margin-top: 8rem;
    margin-left: 9rem;
    &:nth-child(odd) {
      margin-left: 5rem;
    }
    &:nth-child(even) {
      border-left: 1px solid #e5e5e5;
    }
    &:nth-child(1),
    &:nth-child(2) {
      margin-top: 0;
    }
  }
}