.descriptions-product {
  position: relative;
  padding: 12.3rem 0 12rem;
  display: flex;
  &__left {
    flex: 1 1 auto;
  }
  &__title {
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 3.7rem;
    text-transform: uppercase;
    margin-bottom: 3rem;
  }
  &__text {
    margin-bottom: 6rem;
    &.text-content {
      color: #000;
    }
  }
  &__triggers {
    position: relative;
  }
  &__triggers-item {
    margin-bottom: 4.5rem;
    display: flex;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__triggers-icon {
    flex: 0 0 15.2rem;
    max-width: 15.2rem;
    padding: 1.8rem 1.6rem;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 7.4rem;
      height: 7.4rem;
      border-radius: 50%;
      background: #7DA0B8;
      opacity: .1;
    }
  }
  &__triggers-info {
    flex: 1 1 auto;
  }
  &__triggers-title {
    margin-bottom: 1.5rem;
    font-family: 'Bebas';
    font-weight: 700;
    font-size: 2.4rem;
    letter-spacing: 1px;
    text-transform: uppercase;
  }
  &__triggers-descriptions {
    position: relative;
    &.text-content {
      color: #000;
    }
  }
  &__right {
    flex: 0 0 48rem;
    max-width: 48rem;
    margin-left: 11rem;
    overflow: hidden;
    background: #fff;
    box-shadow: 0px .5rem 2rem rgba(0, 0, 0, 0.08);
    border-radius: .8rem;
  }
  &__img {
    height: 40.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
  }
  &__img-descriptions {
    background: $red;
    padding: 4rem 4rem 6rem;
    &.text-content {
      font-size: 1.8rem;
      color: #fff;
    }
  }
}